
@keyframes resolve-day{
    0%{
        opacity: 0;
       
      

    }
    100%{
        opacity: 1;
  
    }
}

@keyframes resolve-night{
    0%{
        opacity: 0;
      
    }
    100%{
        opacity: 1;  

    }
}

///
/// 

@keyframes from-right{
    0%{
        position: absolute;
        top: 0;
        left: 50%;
    }
    100%{
        position: absolute;
        top: 0;
        left: 0%;

    }
}

@keyframes from-left{
    0%{
        position: absolute;
        top: 0;
        right: 50%;
    }
    100%{
        position: absolute;
        top: 0;
        right: 0%;

    }
}

/// SHUTING START ANIMATION///

@keyframes shutting-star {
    0%{
        transform: translateX(-10rem) translateY(-5rem);
       

    }
    100%{
        transform: translateX(100rem) translateY(75rem);
     }
    
};

//plane animation//

@keyframes plane-animation{
    0%{
        transform: translateX(-30rem) translateY(30rem);


    }
    100%{
        transform: translateX(100rem) translateY(-290rem);

    }
}
