
.light {
    background-color: white;
    position: absolute;
    height: 100%;
    width: 100%;

    z-index: 2;
    top: 0rem;
    left: 0rem;
    overflow: hidden;
    
    transition: all 0.3s;
    background-size: contain;
}

.light-img{
    width: 160%;
   
}

.resolve-night{
    animation-name: resolve-night;
    animation-duration: 5s;
}
.resolve-day{
    animation-name: resolve-day;
    animation-duration: 5s;
}



.night-img{
    width: 160%;
   
}

.day-animation{
    animation-name: resolve-day;
    animation-duration: 1s;
    animation-timing-function: linear;
}

.dark{
    background-color: black;
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: 2;

    top: 0rem;
    left: 0rem;
    overflow: hidden;
    transition: all 0.3s;

    background-size: contain;

    
}

.dark-animation{
    animation-name: resolve-night;
    animation-duration: 2s;
    animation-timing-function: linear;
}

.activate-function{
    position: absolute;
    z-index: 3;
    top:50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);



}

.toggle-container{
    position: absolute;
    height: 5rem;
    width: 10rem;
    z-index: 5;
    background-color: rgb(110, 110, 110);
    top:50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);

    border-radius: 3rem;
    border: solid rgb(85, 85, 85) 0.5rem;

}



.ball{
    display: flex;
    justify-content: center;
    align-items: center;
}

.ball-to-right{
    background-color: rgb(150, 150, 150);
    height: 100%;
    width: 45%;
    border-radius: 5rem;
    position: absolute;
    top: 0;
    right: 0%;

    transition: all 0.5;

    animation-name: from-left;
    animation-duration: 0.5s;
    animation-timing-function: ease-in-out;

}

.ball-to-right:hover{
    cursor: pointer;
}

.ball-to-left{
        background-color: rgb(83, 83, 83);
        height: 100%;
        width: 45%;
        border-radius: 5rem;
        position: absolute;
        top: 0;
        left: 0%;

        transition: all 0.5;

        animation-name: from-right;
        animation-duration: 0.5s;
        animation-timing-function: ease-in-out;
}

.ball-to-left:hover{
    cursor: pointer;
}



