/* INNER LIGHT FUNCTIONALITY */
.inner-light{
    border-radius: 4rem;
    z-index: 8;
    height: 1rem;
    width: 1rem;
    background-color: rgb(43, 14, 211);
    transition: all 2s;
}

.light-on{
    border-radius: 4rem;
    z-index: 8;
    height: 1rem;
    width: 1rem;
    box-shadow: 0px 0px 30px 20px rgb(0, 47, 255);

    background-color: rgb(0, 162, 255);

    transition: all 4s;

}

/* change the color of container */
.container-darken{
    background-color: rgb(122, 122, 122);
    border: solid rgb(70, 70, 70) 0.5rem;
    
    
}

/* //shooting start */

.shoot-2{
   position: absolute;
    height: 4rem;
    z-index: 10;
    top: -5rem;
    left: 0rem;
  
}

.shutting-star-animation-class{
    animation-name: shutting-star;
    animation-duration: 1s;
    animation-timing-function: linear;
    
}

//styling plane effect//

.plane{
    height: 2rem;
    position: absolute;
    bottom:0rem;
    right: 20rem;
    z-index: 9;

    animation-name: plane-animation;
    animation-duration:60s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
}